import React from "react"
import {
  Box,
  Accordion,
  AccordionItem,
  AccordionPanel,
  AccordionButton,
  Text,
} from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
import { ChevronRightIcon, ChevronDownIcon } from "@chakra-ui/icons"

const Menu = ({ content, rootTitle }) => {
  const getChevronIcon = isExpanded => {
    return isExpanded ? (
      <ChevronDownIcon boxSize={6} />
    ) : (
      <ChevronRightIcon boxSize={6} />
    )
  }

  const absolutePath = rootTitle.frontmatter.title
  const firstLeftTocElement = absolutePath.replaceAll("TOC", "")

  return (
    <Box>
      <Text
        fontSize={20}
        pl={12}
        pb={1}
        fontWeight={600}
        textTransform="capitalize"
      >
        {firstLeftTocElement}
      </Text>
      {content.map(item => (
        <Accordion allowToggle key={item.textElement} pl={4}>
          <AccordionItem border="none">
            {({ isExpanded }) => (
              <>
                <AccordionButton
                  as={GatsbyLink}
                  to={item.href}
                  _hover={{
                    background: "white",
                  }}
                  pl={item.children.length > 0 ? 0 : 6}
                >
                  <Box pr={2.5}>
                    {item.children.length > 0 && getChevronIcon(isExpanded)}
                  </Box>
                  <Box textAlign="left" fontWeight={600} fontSize={20}>
                    {item.textElement}
                  </Box>
                </AccordionButton>
                {item.children?.map(subItem => (
                  <AccordionPanel key={subItem.textElement} py={0} pl={12}>
                    <AccordionButton
                      _hover={{
                        background: "rlGrey20",
                        borderRadius: 5,
                      }}
                      p={0}
                    >
                      <GatsbyLink
                        to={subItem.href}
                        style={{
                          fontWeight: 500,
                          fontSize: 20,
                          color: "rlGray40",
                          paddingBottom: 3,
                        }}
                      >
                        {subItem.textElement}
                      </GatsbyLink>
                    </AccordionButton>
                  </AccordionPanel>
                ))}
              </>
            )}
          </AccordionItem>
        </Accordion>
      ))}
    </Box>
  )
}

export default Menu
