import React from "react"
import { Flex, Breadcrumb, BreadcrumbItem, Box } from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
import { ChevronRightIcon } from "@chakra-ui/icons"
import getBreadcrumbData from "../../utils/get-breadcrumb-data"

const Breadcrumbs = ({ content, rootTitle, location }) => {
  const absolutePath = rootTitle.frontmatter.title
  const firstBreadcrumbElement = absolutePath.replaceAll("TOC", "")

  const breadcrumbData = getBreadcrumbData(content, location.pathname)

  return (
    <Flex
      justifyContent="flex-start"
      fontSize={14}
      fontWeight={500}
      color="rlGreyDarker"
      mb={2}
      display={{ base: "none", md: "flex" }}
    >
      <Flex>
        <Box fontWeight={500} textTransform="capitalize">
          <GatsbyLink to="#">{firstBreadcrumbElement}</GatsbyLink>
        </Box>
        <ChevronRightIcon color="rlGreyDarker" boxSize={5} mx={2} mt={0.5} />
      </Flex>

      <Breadcrumb
        separator={<ChevronRightIcon color="rlGreyDarker" boxSize={5} />}
      >
        {breadcrumbData.map(crumb => (
          <BreadcrumbItem
            key={crumb.textElement}
            _last={{
              fontWeight: 700,
            }}
          >
            <GatsbyLink to={crumb.href}>{crumb.textElement}</GatsbyLink>
          </BreadcrumbItem>
        ))}
      </Breadcrumb>
    </Flex>
  )
}
export default Breadcrumbs
