import * as React from "react"
import {
  Alert,
  AlertDescription,
  AlertTitle,
  Box,
  Icon,
  Spacer,
  Center,
} from "@chakra-ui/react"
import { getCalloutVariationByType } from "./calloutVariations"
import { getCalloutTypeAndChildren } from "./calloutUtils"

const Callouts = ({ children }) => {
  const { calloutType, updatedChildren } = getCalloutTypeAndChildren(children)
  const currentCalloutVariation = getCalloutVariationByType(calloutType)

  return (
    <Box>
      <Spacer h="5" />
      <Alert
        flexDirection="column"
        alignItems="left"
        borderLeftWidth="10px"
        borderLeftStyle="solid"
        borderLeftColor={currentCalloutVariation.accentColor}
        bg={currentCalloutVariation.baseColor}
      >
        <AlertTitle display="flex" flexDirection="row">
          <Center w="1.5em" h="1.5em">
            <Icon
              as={currentCalloutVariation.icon}
              h={5}
              w={5}
              pr={1}
              color={currentCalloutVariation.iconColor}
            />
          </Center>
          {currentCalloutVariation.title}
        </AlertTitle>
        <AlertDescription>{updatedChildren}</AlertDescription>
      </Alert>
    </Box>
  )
}

export default Callouts
