import * as React from "react"
import { useMemo } from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { graphql } from "gatsby"
import Toc from "../components/toc/toc"
import renderAst from "../utils/render-ast"
import renderHomepageAst from "../utils/render-homepage-ast"
import { Flex, Box, VStack } from "@chakra-ui/react"
import Menu from "../components/menu/menu"
import Breadcrumbs from "../components/breadcrumb/breadcrumb"
import { getTocData } from "../utils/get-toc-data"
import Feedback from "../components/feedback/feedback"

const DocumentationPage = ({ data, location }) => {
  const tocData = useMemo(() => {
    return getTocData(data)
  }, [data])

  const page = data.pageContent
  const pageType = page.fileAbsolutePath.includes("/home-pages/")
    ? "homePage"
    : "docPage"
  return (
    <Layout tocContents={data.rootToc}>
      <Seo title={page.frontmatter.title} />

      {pageType === "docPage" ? (
        <Box p={{ base: 4, md: 8, lg: 10 }} display="flex">
          <Flex w="100%" pt={{ base: "none", md: 16 }} display={{ md: "flex" }}>
            <Box
              pt={5}
              mb={{ base: 10, md: "none" }}
              minW={307}
              mr={{ base: "none", md: "none", lg: 10, xl: 10 }}
            >
              <Menu content={tocData} rootTitle={data.toc} />
            </Box>
            <Box
              flex="1"
              alignItems="center"
              justifyContent="center"
              px={{ md: "0" }}
              maxW="42rem"
            >
              <Breadcrumbs
                content={tocData}
                rootTitle={data.toc}
                location={location}
              />
              <Box>{renderAst(page.htmlAst)}</Box>
            </Box>
            <Box
              display={{ base: "none", md: "none", lg: "flex", xl: "flex" }}
              pt={5}
              ml="3.5em"
              maxW="18em"
            >
              <VStack>
                <Box>
                  <Toc data={data.pageContent} location={location} />
                  <Feedback location={location} />
                </Box>
              </VStack>
            </Box>
          </Flex>
        </Box>
      ) : (
        <Box m={10}>
          <div>{renderHomepageAst(page.htmlAst)}</div>
        </Box>
      )}
    </Layout>
  )
}

export const query = graphql`
  query ($absolutePath: String!, $tocRegex: String!) {
    pageContent: markdownRemark(fileAbsolutePath: { eq: $absolutePath }) {
      html
      htmlAst
      fileAbsolutePath
      frontmatter {
        title
      }
      headings {
        id
        value
        depth
      }
    }
    toc: markdownRemark(fileAbsolutePath: { regex: $tocRegex }) {
      html
      htmlAst
      fileAbsolutePath
      frontmatter {
        title
      }
    }
    rootToc: allFile(filter: { sourceInstanceName: { eq: "homepages" } }) {
      nodes {
        id
        gitRemote {
          sourceInstanceName
        }
        name
      }
    }
  }
`

export default DocumentationPage
