import * as React from "react"
import { Box, Flex, Heading, VStack } from "@chakra-ui/react"
import { navigate } from "gatsby"

const HomepageCard = ({ children }) => {
  const content = children?.find(x => typeof x === "object")?.props?.children
  const text = content?.find(x => typeof x === "string")

  const title = content?.find(x => x.type === "a")
  const titleText = title?.props?.children[0]
  const titleHref = title?.props?.href

  return (
    <VStack
      onClick={() => navigate(titleHref)}
      _hover={{ bg: "rlBlue", cursor: "pointer", color: "white" }}
      data-group
      alignItems="left"
    >
      <Box marginX="2em" marginTop="1em" marginBottom="2em">
        <Heading as="h3" size="md" data-testid="title">
          {titleText}
        </Heading>
        <br />
        <p data-testid="content">{text}</p>
      </Box>
      <Flex w="100%" height="7px" marginTop="auto!important">
        <Box
          height="100%"
          bg="rlBlack"
          w="38%"
          _groupHover={{ background: "rlBlue" }}
        />
        <Box
          height="100%"
          bg="rlBlue"
          w="24%"
          _groupHover={{ background: "rlBlue" }}
        />
        <Box
          height="100%"
          bg="rlGreen"
          w="16%"
          _groupHover={{ background: "rlBlue" }}
        />
        <Box
          height="100%"
          bg="rlYellow"
          w="12%"
          _groupHover={{ background: "rlBlue" }}
        />
        <Box
          height="100%"
          bg="rlIvory"
          w="10%"
          _groupHover={{ background: "rlBlue" }}
        />
      </Flex>
    </VStack>
  )
}

export default HomepageCard
