import * as React from "react"
import RehypeReact from "rehype-react"
import H1 from "../elements/h1";
import H2 from "../elements/h2";
import H3 from "../elements/h3";
import Paragraph from "../elements/paragraph";
import HomepageCard from "../elements/homepageCard/homepageCard";
import HomepageCardContainer from "../elements/homepageCardContainer/homepageCardContainer";

const renderHomepageAst = new RehypeReact({
  createElement: React.createElement,
  components: {
    h1: H1,
    h2: H2,
    h3: H3,
    p: Paragraph,
    blockquote: HomepageCard,
    section: HomepageCardContainer
  },
}).Compiler;

export default renderHomepageAst;