const getData = (nodes, breadcrumb, pathname) => {
  for (const node of nodes) {
    if (node.href === pathname) {
      const newNode = { ...node }
      newNode.children = []
      breadcrumb.push(newNode)
      return
    }

    if (node.children.find(item => pathname.startsWith(item.href))) {
      const newNode = { ...node }
      newNode.children = []
      breadcrumb.push(newNode)
      getData(node.children, breadcrumb, pathname)
      return
    }
  }
}

const getBreadcrumbData = (nodes, pathname) => {
  let breadcrumb = []
  getData(nodes, breadcrumb, pathname)
  return breadcrumb
}

export default getBreadcrumbData
