import QuoteIcon from "../../icons/quoteIcon"
import { InfoOutlineIcon, WarningTwoIcon } from "@chakra-ui/icons"
import DangerIcon from "../../icons/dangerIcon"

/**
 * the attributes for every type of callout
 */
const calloutVariations = {
  "[!note]": {
    icon: QuoteIcon,
    accentColor: "rlBlack40",
    baseColor: "rlIvory40",
    iconColor: "rlBlack40",
  },
  "[!info]": {
    icon: InfoOutlineIcon,
    accentColor: "rlBlue",
    baseColor: "rlBlue10",
    title: "Info",
  },
  "[!warning]": {
    icon: WarningTwoIcon,
    accentColor: "rlYellow",
    baseColor: "rlYellow10",
    title: "Warning",
  },
  "[!danger]": {
    icon: DangerIcon,
    accentColor: "rlRed",
    baseColor: "rlRed10",
    title: "Danger",
  },
  "[!tip]": {
    icon: InfoOutlineIcon,
    accentColor: "rlGreen",
    baseColor: "rlGreen10",
    title: "Tip",
  },
}

const getCalloutVariationByType = type => {
  const variation = calloutVariations[type]

  //if the variation name is valid, return the corresponding variation data, otherwise return the first variation so that the code doesn't break
  if (variation != null) {
    return variation
  } else {
    const defaultVariationType = Object.keys(calloutVariations)[0]
    return calloutVariations[defaultVariationType]
  }
}

export { calloutVariations, getCalloutVariationByType }
