import React from "react"
import { Box, ListItem, List, Heading } from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
const Toc = ({ data, location }) => {
  const list = data.headings

  return (
    <Box>
      {list.length > 1 && (
        <List mb={16}>
          <Heading fontSize={24} fontWeight={700} pb={2.5}>
            Table of contents
          </Heading>
          {list
            .filter(item => item.depth > 1)
            .map(item => (
              <ListItem
                key={item.id}
                mb={item.depth > 2 ? 1 : 3}
                ml={item.depth > 2 ? 4 : 0}
                color={location.hash === `#${item.id}` ? "rlBlue" : "rlGrey"}
              >
                <GatsbyLink to={`#${item.id}`}>{item.value}</GatsbyLink>
              </ListItem>
            ))}
        </List>
      )}
    </Box>
  )
}

export default Toc
