/**
 * returns the indices of the nodes in the AST where the string with the callout type is
 * @param children
 * @returns {{indexOfChildContainingCalloutType: *, textIndex: *}}
 */
const getCalloutTypeIndices = children => {
  const indexOfChildContainingCalloutType = children.findIndex(
    x => typeof x === "object"
  )
  const textIndex = children[
    indexOfChildContainingCalloutType
  ].props.children.findIndex(x => typeof x === "string")

  return { indexOfChildContainingCalloutType, textIndex }
}

/**
 * returns the type of the callout and the children without the text representing the node type
 * @param children
 * @returns {{calloutType: number, updatedChildren: *[]}}
 */
const getCalloutTypeAndChildren = children => {
  const { indexOfChildContainingCalloutType, textIndex } =
    getCalloutTypeIndices(children)

  const text =
    children[indexOfChildContainingCalloutType].props.children[textIndex]
  const regexForCalloutType = "[![A-Za-z]+]"
  const typeMatches = text?.match(regexForCalloutType)
  const calloutType = typeMatches?.["0"]

  let updatedChildren = [...children]
  updatedChildren[indexOfChildContainingCalloutType].props.children[textIndex] =
    updatedChildren[indexOfChildContainingCalloutType].props.children[
      textIndex
    ].replace(calloutType, "")

  return { calloutType, updatedChildren }
}

export { getCalloutTypeAndChildren }
