import * as React from "react"
import RehypeReact from "rehype-react"
import H1 from "../elements/h1"
import H2 from "../elements/h2"
import H3 from "../elements/h3"
import Paragraph from "../elements/paragraph"
import Callout from "../elements/callout/callout"
import Link from "../elements/link"
import Ul from "../elements/ul"
import Li from "../elements/li"
import Ol from "../elements/ol"
import Table from "../elements/table/table"
import Tbody from "../elements/table/tbody"
import Thead from "../elements/table/thead"
import Th from "../elements/table/th"
import Tr from "../elements/table/tr"
import Td from "../elements/table/td"
import CodeBlockCopy from "../elements/codeBlockCopy/codeBlockCopy"
import Pre from "../elements/pre"

const renderAst = new RehypeReact({
  createElement: React.createElement,
  components: {
    h1: H1,
    h2: H2,
    h3: H3,
    p: Paragraph,
    blockquote: Callout,
    a: Link,
    ol: Ol,
    ul: Ul,
    li: Li,
    table: Table,
    tbody: Tbody,
    thead: Thead,
    th: Th,
    tr: Tr,
    td: Td,
    codeblockcopytag: CodeBlockCopy,
    pre: Pre,
  },
}).Compiler

export default renderAst
